body {
  font-family: "Open Sans";
}

input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}


.react-pdf__Page__canvas {
  max-width: 800px;
  width: 100%;
  height: auto;

}

/* Responsive layout - makes a one column layout instead of a two-column layout */
@media (max-width: 800px) {
  .react-pdf__Page__canvas {
    max-width: 400px;
    width: 80%;
    height: auto;

  }
}